@font-face {
  font-family: "Lment";
  src: url("../../fonts/Lment/Lment-v02.otf");
}

@font-face {
  font-family: "Grotesk";
  src: url("../../fonts/Grotesk/Grotesk.otf");
}

@font-face {
  font-family: "Lack";
  src: url("../../fonts/Lack/Lack-Regular.otf");
}

.trails-main {
  width: 80%;
  height: 100%;
  white-space: nowrap;
  text-align: center;
  position: relative;
}

.trails-main:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0%;
  margin-right: -.25em;
  height: 100%;
}

.trails-text {
  display: inline-block;
  vertical-align: middle;
  white-space: normal;
}

.travel {
  font-family: "Lack";
  text-decoration: none;

  display: flex;
  position: absolute;
  justify-content: center;
  align-content: center;
  flex-direction: column;

  right: 5%;
  bottom: 25%;
  width: 92%;
  height: 7%;


  border: 1px solid #fff;
  border-radius: 15px;
  color: white;
  background-color: transparent;
  transition: 0.3s;
  
}

.travel:hover {
  background-color: white;
  color: black;
}

.fadeIn {
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}